import http from "@/utils/request";


export const getLanguages = (data) => {
    return http.post("/translate/languages", data)
}

export const getStrings = (data) => {
    return http.post("/translate/get", data)
}

export const submitStrings = (data) => {
    return http.post("/translate/submit", data)
}

export const formatTranslate = (data) => {
    let config = {
        headers: {"Content-Type": "multipart/form-data"}
    }
    return http.postForm("/translate/formatted", data, config)
}