<template>
  <div class="container">
    <div class="App">
      <el-radio-group v-model="selectedPlatForm" size="small">
        <el-radio plain v-for="(item) in platforms" :label="item" border></el-radio>
      </el-radio-group>

      <el-row :gutter="20" style="margin-top: 20px">

        <el-col :span="getTotalSpanCount()">
          <!--          Source 选项内容-->

            <div style="display: flex; align-items: center; width: 100%;">
              <h3 style="margin-right: 12px">Source</h3>
              <el-select style="flex: 1; margin-right: 10px" v-model="sourceLanguage" filterable>

                <el-option v-for="item in languageData" :key="item.value" :label="item.label"
                           :value="item.value"></el-option>
              </el-select>

              <el-button v-show="isTextModel()" @click="startTranslate()" :loading="isTransLoading" type="primary" size="small"
                         style="font-weight: bold;">
                {{ getTranslateBtnText() }}</el-button>
            </div>

        </el-col>

        <el-col :span="getTotalSpanCount()">

          <!--    target 语言选择内容-->
          <div style="display: flex; align-items: center">
            <h3 style="margin-right: 12px">Targets</h3>

            <el-select style="width: 100%" v-model="languagesArray" multiple filterable collapse-tags placeholder="Selected Languages">
              <el-option v-for="item in languageData" :key="item.value" :label="item.label" :value="item.value">
              </el-option>
            </el-select>
          </div>
        </el-col>

        <el-col :span="getTotalSpanCount()" style="margin-bottom: 10px">


<!--            input内容-->

            <div v-show="isTextModel()" style="display: flex; flex-direction: column; border: 1px solid #e0e0e0;  border-radius: 8px;">
              <el-input
                  type="textarea"
                  class="custom-input"
                  v-model="textInputContent"
                  placeholder="Content" style="width: 100%; height: 100px" resize="none" show-word-limit></el-input>
              <el-input
                  type="textarea"
                  class="custom-input"
                  v-model="textInputContext"
                  placeholder="Context" style="width: 100%; height: 100px; border-top: 1px solid #e0e0e0" resize="none" show-word-limit></el-input>

            </div>

            <div class="drop-area"
                 v-show="!isTextModel()"
                 @dragover.prevent @drop="handleDrop"
                 @dragenter="isDragging = true"
                 @dragleave="isDragging = false"
                 :class="{ 'highlight-border': isDragging }">

              <h4 v-if="!selectedFile" style="text-align: center; margin: auto; width: 100%; font-size: 20px; font-weight: normal; color: #333333">
                {{platformDesc[selectedPlatForm]}}
              </h4>

              <div v-else style="display: flex; flex-direction: column;  width: 60%; margin: auto">
                <div style="display: flex; justify-content: space-between;
                align-items: center;
                background-color: #f1f2f3; width: 100%; border-radius: 10px;  min-height: 48px; ">

                  <div style="display: flex; align-items: center">
                    <i style="margin: 20px" class="el-icon-document-remove"></i>
                    <span>{{ selectedFile.name }}</span>
                  </div>
                  <i style="margin: 20px" class="el-icon-close" @click="selectedFile = null"/>
                </div>

                <div style="display: flex; justify-content: space-between;">
                <span
                    style="font-weight: normal; color: indianred; font-size: 16px; margin-top: 8px">{{ translateErrorDesc }} </span>
                  <el-button @click="handleTransClick()" :loading="isTransLoading" type="primary"
                             style="font-weight: bold; margin-top: 10px">
                    {{ getTranslateBtnText() }}
                  </el-button>
                </div>


              </div>

          </div>

        </el-col>

        <el-col :span="getMaxColSpanCount()" v-if="!selectedFile">
            <div
                style="border: 1px solid #e0e0e0;  border-radius: 8px; min-height: 200px; background: #f5f5f5; display: flex; flex-direction: column">
              <span style="margin: 12px; font-size: 20px; font-weight: normal; color: #333333; white-space: pre-line">{{this.textTransResult}}</span>
            </div>
        </el-col>
      </el-row>

      <div style="display: flex; justify-content: space-between; align-items: self-start;">
        <div>
          <span v-show="isTextModel()"
              style="font-weight: normal; color: indianred; font-size: 16px; margin-top: 8px; width: 40%">{{ translateErrorDesc }} </span>
        </div>
        <div>
          <el-link :href="sendEmail()" style="font-size: 11px; color: #999">Send feedback</el-link>
        </div>
      </div>
    </div>
    <div style="display: flex; flex-direction: column; align-items: center;">
      <span style="padding-top: 100px; padding-bottom: 100px; color: #999999">@Power by AppLocalize.top</span>
    </div>

  </div>
</template>

<script>
import languages from "@/store/languages";
import {formatTranslate} from "@/api/user";


export default {
  data() {
    return {
      platforms: [ "Text", "Android","iOS"],
      platformDesc: {
        "Text": "",
        "Android": "Drag and drop your string.xml",
        "iOS": "Drag and drop your Localizable.strings",
      },
      selectedPlatForm: "Text",
      sourceLanguage: "EN",
      languageData: [],
      languagesArray: ["ZH"],
      isDragging: false,
      selectedFile: null,
      isTransLoading: false,
      translateResult: null,
      translateErrorDesc: null,
      textInputContent: "",
      textInputContext: "",
      isMobile: false,
      textTransResult: "Translation",
    }
  },
  methods: {
    getMaxColSpanCount() {
      if (!this.isMobile) {
        return 12
      } else {
        return 24;
      }
    },
    isTextModel() {
      let isPlainText = "Text" == this.selectedPlatForm
      if (!isPlainText) {
        this.textTransResult = "Translation"
      }
      return isPlainText
    },

    getTranslateBtnText() {
      console.log(this.translateResult != null, "getTranslateBtnText")
      if (this.isTransLoading) {
        return "Translating..."
      }
      if (this.translateResult != null) {
        return "Download translation"
      }
      return "Translate"
    },
    sendEmail() {
      return `mailto:kolacbb@gmail.com?subject=${encodeURIComponent("AppLocalize Feedback")}`;
    },
    getTotalSpanCount() {
      if (this.selectedFile == null) {
        return this.getMaxColSpanCount()
      } else {
        return 24
      }
    },
    handleDrop(event) {
      event.preventDefault();
      this.isDragging = false
      const files = event.dataTransfer.files;
      if (0 < files.length) {
        this.selectedFile = files[0]
        this.translateResult = null;
      }
    },
    handleTransClick() {
      console.log(this.translateResult != null, "handleTransClick")

      if (this.translateResult == null) {
        this.startTranslate()
      } else {
        this.downloadOutput()
      }
    },
    downloadOutput() {
      const downloadLink = document.createElement('a');
      downloadLink.href = URL.createObjectURL(this.translateResult);
      // 设置下载链接的属性
      downloadLink.download = 'values.zip'; // 文件名
      downloadLink.innerHTML = 'Download File'; // 链接文本
      document.body.appendChild(downloadLink);
      downloadLink.click();
      document.body.removeChild(downloadLink);
    },
    startTranslate() {
      this.isTransLoading = true
      this.translateErrorDesc = null
      let isPlainText = this.isTextModel()

      let formData = new FormData()
      if (isPlainText) {
        if (this.textInputContent == null || this.textInputContent == "") {
          this.translateErrorDesc = "Need to enter the content to be translated.";
          this.isTransLoading = false
          return
        }
        formData.append("files", this.stringToBlob(
            JSON.stringify(
              {"text":this.textInputContent, "context": this.textInputContext}
            )
        ))
      } else {
        formData.append("files", this.selectedFile)
      }
      formData.append("source", this.sourceLanguage)
      formData.append("platform", this.selectedPlatForm)
      formData.append("targets", this.languagesArray + "," + this.sourceLanguage);

      formatTranslate(formData).then((data) => {
        this.isTransLoading = false
        if (data.code == 200 && data.data != null) {
          console.log(data.code == 200, data.data != null)
          if (isPlainText) {
            this.textTransResult = data.data;
          } else {
            this.translateResult = this.base64ToBlob(data.data)
          }
          console.log(this.translateResult != null, data)
        } else {
          this.translateErrorDesc = data.msg
        }
      })
    },
    stringToBlob(binaryString) {
      console.log(binaryString)
      const encoder = new TextEncoder();
      const encodedContent = encoder.encode(binaryString);
      // 创建Blob对象
      return new Blob([encodedContent], {type: 'text/plain'});
    },
    base64ToBlob(base64Data) {
      // 将Base64编码的内容转换为Uint8Array
      const binaryString = window.atob(base64Data);
      const bytes = new Uint8Array(binaryString.length);
      for (let i = 0; i < binaryString.length; i++) {
        bytes[i] = binaryString.charCodeAt(i);
      }
      // 创建Blob对象
      return new Blob([bytes], {type: 'application/zip'});
    },
    checkMobile() {
      // 根据窗口宽度判断是否为移动设备
      this.isMobile = window.innerWidth < 768;
    }
  },
  mounted() {
    const data = [];
    Object.keys(languages.state.allLanguages).forEach((item) => {
      data.push({
        label: languages.state.allLanguages[item],
        value: languages.state.languageKey[item],
      })
    })

    this.languageData = data;

    this.isMobile = window.innerWidth < 768;
    window.addEventListener('resize', this.checkMobile);

  }
}
</script>

